@import '../../../scss/variable.scss';
@import '../../../scss/mixin.scss';

@font-face {
  font-family: 'SFPRORegular';
  src: url(../../../assets/fonts/SFProText/SFProText-Regular.ttf) format("opentype");
}

.wrapper {
  overflow: hidden;
  user-select: none;
  position: relative;
  margin-top: 20px;
  padding: 74px 20px 20px 20px;
  background: #CFE2FF;
  overflow: hidden;
  border-radius: 40px;
  max-width: 881px;
  width: 100%;
  animation: slideDown 0.2s ease-in forwards;

  &_question_count {
    @include font(500, 26px, 31.03px);
    color: $color-text;
    text-align: center;
    font-family: 'SFPRORegular';
  }

  &_drop_down_menu {
    margin-top: 10px;
    padding: 20px 21px 30px 36px;
    background: url(../../../assets/img/common/questions.png), linear-gradient(
      140deg,#c2e0ff 0%, #c2e0ff 11%, #d6eaff 16%, #e5f2ff 25%, #fafcff 57%, #fafcff 82%, #e5f2ff 89%, #d6eaff 93%, #c2e0ff 100%);
    background-repeat: no-repeat;
    background-position: right;
    background-size: contain;
    box-shadow: 0px 0px 8px 0px #3241954D;
    overflow: hidden;
    border-radius: 20px;
    min-height: 409px;
    max-width: 841px;
    width: 100%;
    animation: slideDown 0.2s ease-in forwards;
    @media only screen and (min-width : 320px) and (max-width : 375px) {
      padding: 5%;
    }
    @media only screen and (min-width : 375px) and (max-width : 480px) {
      padding: 5%;
    }

    &_question_count {
      @include font(700, 20px, 23.87px);
      color: $color-text;
      text-align: center;
      font-family: 'SFPRORegular';
      margin-bottom: 33.5px;
    }
  }

  @keyframes slideDown {
    from {
      transform: scaleY(0);
    }
    to {
      transform: scaleY(1);
    }
  }
}
